@font-face {
  font-family: RubikLightItalic;
  src: url(Rubik-LightItalic.ttf);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e9efcb;
}

.chat-text-font-face {
  /*font-family: RubikLightItalic, Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;*/
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.chat-item-main {
  font-size: 30pt;
  margin-bottom: 10px;
  color: rgb(119 109 109);
}

.chat-item-reply {
  font-size: 22pt;
  margin-bottom: 10px;
}

.chat-text-box {
  background: transparent;
  border-style: none;

  outline: 2px solid transparent;
  outline-offset: 2px;

  line-height: 1.7rem;

  height: 38px !important;

  resize: none;

  font-size: 20pt;
}

.top-menu-item {
  margin-right: 20px;;
}

@media screen and (max-width: 780px) {
  .chat-item-main {
    font-size: 20pt;
  }

  .chat-item-reply {
    font-size: 15pt;
  }

  .chat-text-box {
    font-size: 15pt;
  }
}